import { GET_PREDICTION, PREDICTION_RESULT } from '../actions/types'

const initialState = {
    predictions: [],
    predictionResult: ''
}

export default function Reducer( state=initialState, action ) {
    switch (action.type) {
        case GET_PREDICTION:
            return {
                ...state,
                predictions: action.payload,
            };

        case PREDICTION_RESULT:
            return {
                ...state,
                predictionResult: action.payload,
            };

        default:
            return state;
    }
}