import axios from 'axios';
import { GET_PREDICTION, PREDICTION_RESULT } from './types';

export const getPrediction = () => dispatch => {
    return axios
    .get('https://juxtaposefactcheck.herokuapp.com/api/v1/predictions')
    .then(
        res => {
            console.log(res)
            dispatch( {type: GET_PREDICTION, payload: res.data} )
        }
    )
    .catch(
        err => { console.log(err) }
    )
}

export const postPrediction = (title, maintext, source, link) => dispatch => {
    return axios
    .post('https://juxtaposefactcheck.herokuapp.com/api/v1/newprediction', {
        title,
        maintext,
        source,
        link
    })
    .then(
        res => {
            dispatch( {type: PREDICTION_RESULT, payload: res.data} )
        }
    )
    .catch(
        err => { console.log(err) }
    )
}